<template>
  <div class="tag">
    <fx-card title="基础用法">
      <fx-tag v-for="item in baseTag" :key="item.name" :type="item.type">
        {{ item.name }}
      </fx-tag>
      <template #hide>
        <pre v-highlight="base"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="可移除">
      <fx-tag v-for="item in baseTag" :key="item.name" :type="item.type" closable>
        {{ item.name }}
      </fx-tag>
      <template #hide>
        <pre v-highlight="clear"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="不同尺寸">
      <fx-tag v-for="item in sizeTag" :key="item.name" :size="item.size" closable>
        {{ item.name }}
      </fx-tag>
      <template #hide>
        <pre v-highlight="size"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="不同主题">
      <ul class="list">
        <li>
          <fx-tag v-for="item in baseTag" :key="item.name" :type="item.type" theme="light">
            {{ item.name }}
          </fx-tag>
        </li>
        <li>
          <fx-tag v-for="item in baseTag" :key="item.name" :type="item.type" theme="plain">
            {{ item.name }}
          </fx-tag>
        </li>
        <li>
          <fx-tag v-for="item in baseTag" :key="item.name" :type="item.type" theme="dark">
            {{ item.name }}
          </fx-tag>
        </li>
      </ul>
      <template #hide>
        <pre v-highlight="theme"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="动态编辑">
      <fx-tag v-for="item in editTag" :key="item" closable @close="closeTagClick(item)">
        {{ item }}
      </fx-tag>
      <fx-input width="90" v-if="add" v-model="addInput" auto-focus @keydown.stop.enter="addTag"></fx-input>
      <fx-button v-else @click="add=true">+添加标签</fx-button>
      <template #hide>
        <pre v-highlight="edit"><code></code></pre>
      </template>
    </fx-card>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref } from 'vue'

export default defineComponent({
  name: 'viewTag',
  setup () {
    const baseTag = reactive([
      {
        name: '标签一',
        type: 'primary'
      },
      {
        name: '标签二',
        type: 'success'
      },
      {
        name: '标签三',
        type: 'warning'
      },
      {
        name: '标签四',
        type: 'error'
      }
    ])
    const sizeTag = reactive([
      {
        name: '标签一',
        size: 'large'
      },
      {
        name: '标签二',
        size: 'middle'
      },
      {
        name: '标签三',
        size: 'default'
      },
      {
        name: '标签四',
        size: 'mini'
      }
    ])
    const editTag = reactive<string[]>(['标签一', '标签二', '标签三', '标签四'])
    const addInput = ref<string>('')
    const add = ref<boolean>(false)

    const closeTagClick = (val: string) => {
      editTag.splice(editTag.indexOf(val), 1)
    }

    const addTag = () => {
      if (addInput.value === '') return
      editTag.push(addInput.value)
      addInput.value = ''
      add.value = false
    }

    const base = `<fx-tag v-for="item in baseTag" :key="item.name" :type="item.type">
  {{ item.name }}
</fx-tag>`
    const clear = `<fx-tag v-for="item in baseTag" :key="item.name" :type="item.type" closable>
  {{ item.name }}
</fx-tag>`
    const size = `<fx-tag v-for="item in sizeTag" :key="item.name" :size="item.size" closable>
  {{ item.name }}
</fx-tag>`
    const theme = `<ul class="list">
  <li>
    <fx-tag v-for="item in baseTag" :key="item.name" :type="item.type" theme="light">
      {{ item.name }}
    </fx-tag>
  </li>
  <li>
    <fx-tag v-for="item in baseTag" :key="item.name" :type="item.type" theme="plain">
      {{ item.name }}
    </fx-tag>
  </li>
  <li>
    <fx-tag v-for="item in baseTag" :key="item.name" :type="item.type" theme="dark">
      {{ item.name }}
    </fx-tag>
  </li>
</ul>`
    const edit = `<fx-tag v-for="item in editTag" :key="item" closable @close="closeTagClick(item)">
  {{ item }}
</fx-tag>
<fx-input width="90" v-if="add" v-model="addInput" auto-focus @keydown.stop.enter="addTag"></fx-input>
<fx-button v-else @click="add=true">+添加标签</fx-button>

export default defineComponent({
    setup() {
      const editTag = reactive<string[]>(['标签一', '标签二', '标签三', '标签四'])
      const addInput = ref<string>('')
      const add = ref<boolean>(false)

      const closeTagClick = (val: string) => {
        editTag.splice(editTag.indexOf(val), 1)
      }

      const addTag = () => {
        if (addInput.value === '') return
        editTag.push(addInput.value)
        addInput.value = ''
        add.value = false
      }
    }
})`

    return {
      baseTag,
      sizeTag,
      editTag,
      add,
      addInput,
      base,
      clear,
      size,
      theme,
      edit,
      addTag,
      closeTagClick
    }
  }
})
</script>

<style scoped lang="scss">
.tag {
  .list {
    > li {
      margin-bottom: 20px;
    }
  }
}
</style>
